import React from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import intlHelper from '@navikt/sif-common-core/lib/utils/intlUtils';
import { DateRange } from '@navikt/sif-common-utils/lib';
import dayjs from 'dayjs';
import { Element, Normaltekst } from 'nav-frontend-typografi';

interface Props {
    headingLevel: number;
    mned: DateRange;
    antallDagerMedTid: number;
}

const ArbeidstidMnedTittel: React.FunctionComponent<Props> = ({ headingLevel, mned, antallDagerMedTid }) => {
    const intl = useIntl();
    return (
        <Element tag={`h${headingLevel}`}>
            <span className="m-caps">
                {intlHelper(intl, 'arbeidstidMnedTittel.ukeOgr', {
                    ukeOgr: dayjs(mned.from).format('MMMM YYYY'),
                })}
            </span>
            <Normaltekst tag="div">
                {antallDagerMedTid === 0 ? (
                    <FormattedMessage id="arbeidstidMnedTittel.iPeriodePanel.info.ingenDager" />
                ) : (
                    <FormattedMessage
                        id="arbeidstidMnedTittel.iPeriodePanel.info"
                        values={{ dager: antallDagerMedTid }}
                    />
                )}
            </Normaltekst>
        </Element>
    );
};

export default ArbeidstidMnedTittel;
